.news--image {
  max-width: 100%;
   /*  height: 16em; */
    object-fit: cover;
}
  
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  max-width: 500px;
  margin: auto 1rem;
  text-align: center;
  font-family: arial;
}
  
.price {
  color: grey;
  font-size: 22px;
}
  
.card button {
  border: none;
  outline: 0;
  padding: 12px;
  color: white;
  background-color: #000;
  text-align: center;
  cursor: pointer;
  width: 100%;
  font-size: 18px;
}
  
.card button:hover {
  opacity: 0.7;
}